import React from 'react';
import UI from 'components/UI';
import CustomButton from 'components/main/items/ui/CustomButton';


class GetNewAccount extends UI {

  constructor(props){
    super(props);
    this.init(props);
    this.state = {
      codeType: 'course'
    }
  }

  render(){
    this.init(this.props);
    return(
      <div style={this.props.animatedStyle}>
        {this.codeOptions()}
        {this.inputs.inputField('code','text',
        this.state.codeType === 'course'?
        ['Enter class code','','']:
        ['Enter school code','',''], '')}
        <CustomButton app={this.app} button={this.buttons.rectGreen(['Acquire new account','',''], ()=>this.actions.user.getNewAccountByCode(document.getElementById('code').value, this.state.codeType))}/>
        <CustomButton app={this.app} button={this.buttons.rectRed(['Cancel','',''], ()=>this.actions.main.setStatus('waitForLogin'))}/>
      </div>
    )
  }

  codeOptions(){
    const optionsStyle = {...this.bs, ...{
      width: '',
      height: '',
      justifyContent: 'center'
    }}
    return(
      <div style={optionsStyle}>
        {this.checkBox(this.func.multiLang('Get student account by class code','',''), this.state.codeType === 'course', (e)=>{this.setState({ codeType: 'course' })})}
        {this.gap(this.bs.height * 0.015)}
        {this.checkBox(this.func.multiLang('Get teacher account by school code','',''), this.state.codeType === 'school', (e)=>{this.setState({ codeType: 'school' })})}
        {this.gap(this.bs.height * 0.015)}
      </div>
    )
  }

}

export default GetNewAccount;



