import React from 'react';
import View from 'components/main/pages/home/views/View';
import CustomButton from 'components/main/items/ui/CustomButton';

class Account extends View {

  render() {
    this.init(this.props);
    const user = this.store.user;
    const student = this.func.multiLang('Student','','');
    const teacher = this.func.multiLang('Teacher','','');
    const forceAccount = this.store.content.view === 'forceAccount';

    return(
      <div style={this.viewStyle()}>
        {this.gap('4%')}

        {forceAccount && this.subTitle(['Please setup your account!','!','!'], this.bs.height * 0.03)}
        {forceAccount && this.sep()}
        {forceAccount && this.gap(this.bs.height * 0.04)}

        {!forceAccount && this.subTitle(['User type','',''])}
        {!forceAccount && this.sep()}
        {!forceAccount && this.gap('2%')}
        {!forceAccount && user.type !== 'admin' && this.inputs.optionBar('type', ['25%', this.bs.height * 0.04], [student, teacher], user.type === 'student'? student: teacher)}
        {!forceAccount && user.type === 'admin' && this.textDisplay(this.func.multiLang('Admin','',''), ['100%',  this.bs.height * 0.03])}
        {!forceAccount && this.gap('2%')}

        {this.subTitle(['Identity','',''])}
        {this.sep()}
        {this.inputs.inputField('id','text','', user.id)}
        {this.gap('2%')}

        {this.subTitle(['Email address','',''])}
        {this.sep()}
        {this.inputs.inputField('email','text','', user.email)}
        {this.gap('2%')}

        {this.subTitle(['New password','',''])}
        {this.sep()}
        {this.inputs.inputField('newPw','password', forceAccount?'':['Leave it blank if no change','',''],'')}
        {this.gap('2%')}

        {this.subTitle(['Confirm new password','',''])}
        {this.sep()}
        {this.inputs.inputField('confirmPw','password',forceAccount?'':['Leave it blank if no change','',''],'')}
        {this.gap('4%')}

        {!forceAccount && this.subTitle(['mlanghku identity','mlanghku','mlanghku'])}
        {!forceAccount && this.sep()}
        {!forceAccount && this.inputs.inputField('mlanghkuId','text',
        ['Leave it blank if you are not mlanghku user','mlanghku','mlanghku'], user.mlanghkuId)}
        {!forceAccount && this.gap('4%')}

        {!forceAccount && this.subTitle(['mlanghku password','mlanghku','mlanghku'])}
        {!forceAccount && this.sep()}
        {!forceAccount && this.inputs.inputField('mlanghkuPw','password',
        ['Leave it blank if you are not mlanghku user','mlanghku','mlanghku'], user.mlanghkuPw)}
        {!forceAccount && this.gap('4%')}

        {!forceAccount && this.subTitle(['Enter current password to change','',''])}
        {!forceAccount && this.sep()}
        {!forceAccount && this.inputs.inputField('pw','password','','')}
        <CustomButton app={this.app} button={this.buttons.rectRed(['Confirm change','',''], ()=>{this.changing()})}/>
        {this.gap('8%')}
      </div>
    )
  }

  changing(){
    const user = this.store.user;
    const forceAccount = this.store.content.view === 'forceAccount';

    const selecter =  document.getElementById('type')
    const selected = selecter? selecter.selectedIndex: -1;
    const newType =
    selected === 0? 'student':
    selected === 1? 'teacher':
    null;

    const newId = document.getElementById('id').value;
    const newEmail = document.getElementById('email').value;
    const newPw = document.getElementById('newPw').value;
    const confirmPw = document.getElementById('confirmPw').value;
    const pwField = document.getElementById('pw');
    const pw = pwField? pwField.value: '';

    const mlanghkuId = forceAccount? '': document.getElementById('mlanghkuId').value;
    const mlanghkuPw = forceAccount? '': document.getElementById('mlanghkuPw').value;

    //console.log(newId)
    if(forceAccount && newId === 'DefaultId'){
      return this.failedMessage(['Failed to change! Please enter your new identity!', '! !', '! !'])
    }
    if(newId.length < 5){
      return this.failedMessage(['Failed to change! Identity must be atlease 5 characters long!', '! !', '! !'])
    }
    if(!newEmail.includes('@')){
      return this.failedMessage(['Failed to change! Invalid email address!', '! !', '! !'])
    }
    if(forceAccount && newPw.length === 0){
      return this.failedMessage(['Failed to change! Please enter your new password!', '! !', '! !'])
    }
    if(newPw.length > 0){
      if(newPw !== confirmPw){
        return this.failedMessage(['Failed to change! Confirm password not equal to new password!', '! !', '! !'])
      }
      if(newPw.length < 6){
        return this.failedMessage(['Failed to change! Password must be atlease 6 characters long!', '! !', '! !'])
      }
    }
    if(!forceAccount && pw !== user.pw){
      return this.failedMessage(['Failed to change! Please enter your password correctly!', '! !', '! !'])
    }
    if(newType === 'teacher' && (!this.store.profile.joinedSchools || this.store.profile.joinedSchools.length === 0)){
      return this.failedMessage(['Failed to change! You must join a school before switching to teacher!', '! !', '! !'])
    }

    this.actions.user.changeUserInfo({
      _id: user._id,
      type: newType? newType: user.type,
      id: newId,
      pw: newPw.length >= 6? newPw: user.pw,
      email: newEmail,
      mlanghkuId: mlanghkuId? mlanghkuId: user.mlanghkuId,
      mlanghkuPw: mlanghkuPw? mlanghkuPw: user.mlanghkuPw
    });

  }

}

export default Account;



