import React from 'react';
import View from 'components/main/pages/home/views/View';

class Credit extends View {

  creditTextDisplay(text){
    return this.textDisplay(this.func.multiLang(text[0],text[1],text[2]), ['100%',''], '125%', 'center');
  }

  render() {
    this.init(this.props);
    return(
      <div style={this.viewStyle()}>
        {this.gap('4%')}

        {this.subTitle(['Organization','',''])}
        {this.sep()}
        {this.creditTextDisplay(
          ['The University of Hong Kong',
          '',
          ''])}
        {this.creditTextDisplay(
          ['Hong Kong Applied Science and Technology Research Institute',
          '',
          ''])}
        {this.gap('8%')}

        {this.subTitle(['Director','',''])}
        {this.sep()}
        {this.creditTextDisplay(
          ['Dr. Elizabeth Loh',
          '',
          ''])}
        {this.creditTextDisplay(
          ['Dr. Vincent Lau',
          '',
          ''])}
        {this.creditTextDisplay(
          ['Dr. W.W. Ki',
          '',
          ''])}
        {this.creditTextDisplay(
          ['Dr. K.C. Lau',
          '',
          ''])}
        {this.gap('4%')}

        {this.subTitle(['Developer','',''])}
        {this.sep()}
        {this.creditTextDisplay(
          ['Mr. Wong Yan Kit',
          '',
          ''])}
        {this.gap('4%')}
      </div>
    )
  }
}

export default Credit;



